*{
    padding:0;
    margin:0;
}

.Nav-Parent{
    width:100%;
}

.Nav-Child{
    display:flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding:0.5cm 1cm;
    border-bottom: thin solid grey;
}

.Menu-Parent{
    width:100%;
}

.Menu-Child{
    display: flex;
    flex-direction: row;
    column-gap: 0.5cm;
    align-items:center;
}